/*----------------------------- MAIN STYLES ------------------------------- */
/*------------------------------------------------------------------------- */
a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

svg { width: 100%; height: 100%; }

.is-hidden { display: none; }

body { min-width: 320px; font-family: "OpenSans Regular", Arial, sans-serif; font-size: 14px; line-height: 1.42857; }

.wrapper { overflow: hidden; min-height: 100%; }

.container { margin: 0 auto; max-width: 1170px; }

.row:after { content: ""; display: table; clear: both; }

.header { position: fixed; left: 0; top: 0; right: 0; padding-top: 40px; padding-bottom: 40px; background-color: transparent; z-index: 1000; transition: all 0.3s; }
.header.is-fixed { padding-top: 5px; padding-bottom: 5px; background-color: rgba(0, 0, 0, 0.75); transition: all 0.3s; }

.logo { float: left; font-family: "OpenSans Bold", Arial, sans-serif; font-size: 2.25em; text-transform: uppercase; letter-spacing: 0.025em; }
.logo span { display: inline-block; vertical-align: bottom; margin-right: -3px; width: 52px; height: 52px; text-align: center; line-height: 53px; background-color: #F4F5F9; color: #4A4C55; transition: all 0.3s; }
.logo span + span { width: 96px; font-family: "OpenSans Light", Arial, sans-serif; line-height: 48px; background-color: transparent; color: #F1EBE7; border: 3px solid #F4F5F9; transition: all 0.3s; }
.logo:hover span { color: #fff; background-color: #E74C3C; transition: all 0.3s; }
.logo:hover span + span { color: #E74C3C; background-color: transparent; border-color: #E74C3C; transition: all 0.3s; }

.nav { float: right; font-family: "OpenSans Bold", Arial, sans-serif; line-height: 20px; text-transform: uppercase; }
.nav:after { content: ""; display: table; clear: both; }
.nav li { float: left; }
.nav li a { position: relative; display: block; color: #F4F5F9; padding: 15px 24px 17px; transition: all 0.3s; }
.nav li a:after { content: ""; position: absolute; left: 50%; bottom: 0; height: 2px; width: 0; background-color: #E74C3C; transition: all 0.5s; }
.nav li a:hover { color: #E74C3C; transition: all 0.3s; }
.nav li a:hover:after { left: 0; width: 100%; transition: all 0.5s; }

/* Main Slider */
.main-slider { position: relative; height: calc(100% - 25px); background: #fff url("../img/img1.jpg") center top no-repeat; background-size: cover; /* triangles */ }
.main-slider:after { content: ""; position: absolute; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.5); z-index: 1; }
.main-slider .tr-left, .main-slider .tr-right { z-index: 2; position: absolute; bottom: -1px; height: auto; width: calc(50% + 2px); }
.main-slider .tr-left { left: 0; }
.main-slider .tr-right { right: 0; }
.main-slider .bottom-wrap { position: absolute; left: 50%; bottom: -25px; margin-left: -37px; width: 74px; height: 74px; border-radius: 50%; border: 4px solid rgba(255, 255, 255, 0.5); animation: color 2s infinite; }
.main-slider .bottom-btn { position: absolute; left: 50%; bottom: -21px; margin-left: -33px; width: 66px; height: 66px; border-radius: 50%; background-color: #F4F5F9; z-index: 2; transition: all 0.3s; }
.main-slider .bottom-btn .icon { position: absolute; top: 50%; left: 50%; animation: move 2s infinite; background-image: url("../img/icons.png"); width: 22px; height: 31px; background-position: 0px 0px; background-size: 22px 31px; }
.main-slider .bottom-btn:hover { box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5); transition: all 0.3s; }

.slider { position: relative; height: 100%; z-index: 2; }
.slider-el { color: #f4f5f9; height: 100%; white-space: nowrap; }
.slider-el:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; margin-right: -0.25em; }
.slider-el p { white-space: normal; }
.slider-el__in { display: inline-block; width: 100%; vertical-align: middle; text-align: center; }
.slider-el__desc { margin-bottom: 45px; font-family: "SourceSans Pro Regular", Arial, sans-serif; font-size: 1.5em; letter-spacing: 0.05em; }
.slider-el__title { margin-bottom: 45px; font-family: "OpenSans Bold", Arial, sans-serif; font-size: 4.5em; letter-spacing: 0.1em; text-transform: uppercase; }
.slider-el__text { margin-bottom: 45px; margin-left: auto; margin-right: auto; width: 50%; font-family: "OpenSans Regular", Arial, sans-serif; color: #cbc6c0; }
.slider__border { margin-bottom: 45px; text-align: center; }
.slider__border-in { position: relative; display: inline-block; width: 7px; height: 7px; background-color: #e74c3c; border-radius: 50%; }
.slider__border-in:before, .slider__border-in:after { content: ""; position: absolute; top: 3px; width: 80px; height: 1px; background-color: #f1ebe7; }
.slider__border-in:before { left: 100%; margin-left: 15px; }
.slider__border-in:after { right: 100%; margin-right: 15px; }

/* List */
.list { position: relative; z-index: 1; padding-top: 125px; height: 500px; background-color: #fff; }
.list ul:after { content: ""; display: table; clear: both; }
.list ul li { float: left; padding-left: 15px; padding-right: 15px; width: 25%; text-align: center; }
.list__img { display: inline-block; margin-bottom: 30px; width: 73px; height: 73px; border: 1px solid #e5e5e5; white-space: nowrap; }
.list__img:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; margin-right: -0.25em; }
.list__img i[class*="icon"] { font-size: 30px; color: #e74c3c; vertical-align: middle; }
.list__title { position: relative; margin-bottom: 40px; font-family: "OpenSans Semibold", Arial, sans-serif; font-size: 1.125em; color: #6a6a6a; text-transform: uppercase; }
.list__title:after { content: ""; position: absolute; left: 50%; bottom: -20px; margin-left: -20px; width: 40px; height: 2px; background-color: #888; border-bottom: 1px solid #e1e1e1; }

/* Keyframes */
@keyframes color { 0% { border-color: rgba(255, 255, 255, 0.5); }
  50% { border-color: rgba(255, 255, 255, 0); }
  100% { border-color: rgba(255, 255, 255, 0.5); } }
@keyframes move { 0% { transform: translate(-50%, -65%); }
  50% { transform: translate(-50%, -35%); }
  100% { transform: translate(-50%, -65%); } }


